.rankDetails_head {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background: #16235b;
  position: absolute;
  z-index: 5;
}
.rankDetails_head img {
  width: 36px;
}
.rank_details_top {
  position: absolute;
  top: 50px;
  width: 100%;
  z-index: 4;
  background: #0d163f;
}
.rankDetails_info {
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
}
.rankDetails_info_image {
  max-width: 140px;
  width: 30%;
  padding: 16px;
  height: 100%;
  display: flex;
  align-items: center;
}
.rankDetails_info_image img {
  width: 100%;
}
.rankDetails_info_text {
  margin-left: 8px;
  width: 60%;
  color: #f7f1db;
}
.rankDetails_info_text div {
  margin-top: 8px;
}
.rankDetails_info_text>div:nth-child(1) {
  font-size: 19px;
  font-weight: bold;
}
.rankDetails_button {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
}
.rankDetails_button button {
  margin: 0;
  padding: 0;
  border: 0px solid transparent;
  border-top: 1px solid #0d163f;
  outline: none;
  width: 100%;
  height: 41px;
  background: #16235b;
  color: #f7f1db;
}
.play_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.play_btn img {
  margin-left: 4px;
}
.play_btn span {
  line-height: 16px;
  font-size: 16px;
}
.rankDetails_song_list_title {
  color: #a29e92;
  font-size: 13px;
  padding: 8px 0 8px 16px;
  background: #0d163f;
}
.rankDetails_song_list_container {
  color: #a29e92;
}
.rankDetails_song_list_container>li:nth-child(1),
.rankDetails_song_list_container>li:nth-child(2),
.rankDetails_song_list_container>li:nth-child(3) {
  color: #f7f1db !important;
}
.rankDetails_song_list_item {
  display: flex;
  justify-content: space-between;
  padding: 8px 0 8px 0;
  background: #0d163f;
}
.rankDetails_song_list_item:nth-child(odd) {
  background: #16235b;
}
.rankDetails_song_list_item_rank {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.rankDetails_song_list_item_song {
  width: 100%;
}
.rankDetails_song_list_item_song>div:nth-child(1) {
  margin-bottom: 8px;
}
.rankDetails_song_list_item_song>div:nth-child(2) {
  font-size: 12px;
}
.rankDetails_song_list_item_tools {
  display: flex;
  align-items: center;
}
.singer_details_add {
  margin-left: 4px;
  width: 24px;
}
.singer_details_love {
  width: 24px;
  margin-left: 10px;
}
